@charset "utf=8";

body{
  font-family:"맑은 고딕",arial, sans-serif;
  color:#333;
}
.title0{
  text-align: center;
  padding: 1rem;
  border-bottom: 1px solid#ccc;
}
.title{
  text-align: center;
  margin: 0 auto;
  font-size: 26px;
  color: #333;
}

.tab_con_wrap *{margin:0;padding:0;}
.tab_content a{
  text-decoration: none;
  color: inherit;
  display:block;
}
.tab_content a:hover{color:inherit;}

.tab_con_wrap{width:1200px;margin:50px auto;}
.tab_con_wrap li{list-style:none;}

.tab_mnu{
  display:flex;
  justify-content:center;
  margin:50px auto;
}
.tab_mnu .tab{padding:10px 20px;cursor:pointer;}
.tab_mnu .tab.active{color:#4567ad;font-weight:bold;}

.tab_content_info{
  display:flex;
  justify-content:space-between;
  gap:20px;
}
.tab_content_info li{
  width:285px;
  display:flex;
  flex-direction:column;
  position:relative;
}
.tab_content_info img{
  width:100%;
  height:195px;
  object-fit:cover;
}
.tab_content_info p{
  line-height:1.4;
  margin:8px auto;
  width:90%;
  overflow:hidden;
  display:-webkit-box;
  -webkit-line-clamp:2;
  -webkit-box-orient:vertical;
  text-overflow:ellipsis;
}
.tab_content_info p:last-of-type{
  font-weight:bold;
  font-size:20px;
  color:#ff5c46;
  line-height:1.4;
  overflow:visible;
  display:block;
  text-align:right;
}
.tab_content_info p span{font-size:12px;}

.content{display:none;}
.content.active2{display:block;}

/* 배지 공통 */
.badge{
  position:absolute;
  top:0;
  left:0;
  width:60px;
  height:60px;
  box-sizing:border-box;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  font-size:12px;
  font-weight:700;
  line-height:1.2;
  color:#fff;
  text-align:center;
}
.badge.blue{background:#3859b0;}
.badge.red{background:#ff5a4f;left:60px;}
.badge.green{background:#A6CC25;left:60px;}
.badge.black{background:#222;left:60px;text-transform:uppercase;}
